
.pagination-nav .pagination {
  list-style: none;
}
.pagination-nav li {
  float: left;
  border: 1px solid #d7dde4;
  background: #fff;
  text-align: center;
  min-width: 32px;
  height: 32px;
  line-height: 32px;
  border-radius: 4px;
  color: #657180;
  margin-left: 4px;
}
.pagination-nav li.active {
  background: #39f;
  border-color: #39f;
  color: #fff;
}
.pagination-nav li:hover {
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  border-color: #39f;
  color: #39f;
}



