
.input[type="text"],
.input[type="password"] {
  width: 100%;
  height: 40px;
  border: 1px solid #d7dde4;
  font-size: 12px;
  padding: 0 10px;
  box-sizing: border-box;
  color: #657180;
  border-radius: 4px;
}
.input[type="text"]:focus,
  .input[type="password"]:focus {
    border-color: #5cadff;
    outline-color: #5cadff;
}
.form-group {
  margin-bottom: 10px;
  display: inline-block;
}
.form-button_group {
  margin-top: 20px;
}
textarea {
  display: block;
  width: 100%;
  border: 1px solid #d7dde4;
  border-radius: 6px;
  color: #657180;
  padding: 10px;
  box-sizing: border-box;
}
textarea:focus {
    border-color: #5cadff;
    outline-color: #5cadff;
}
.input-big {
  width: 100%;
}
.input-before_label {
  color: #657180;
  display: inline-block;
  margin: 4px 0;
}
.btn {
  color: #fff;
  padding: 6px 12px;
  border-radius: 4px;
  border: 1px solid transparent;
  cursor: pointer;
}
.btn-danger {
  background: #ff3300;
  border-color: #ff3300;
}
.btn-primary {
  background: #3399ff;
  border-color: #3399ff;
}
.btn-success {
  background: #00cc66;
  border-color: #00cc66;
}
.modal .modal-mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(55, 55, 55, 0.6);
  height: 100%;
  z-index: 1000;
}
.modal .modal-wrap {
  position: fixed;
  top: 30%;
  left: 40%;
  background: #fff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
  z-index: 1001;
  border-radius: 6px;
  min-width: 300px;
}
.modal .modal-close {
  color: #999;
  position: absolute;
  right: 0;
  top: 4px;
  cursor: pointer;
}
.modal .modal-content_header,
.modal .modal-content_main,
.modal .modal-content_footer {
  padding: 12px 18px;
}
.modal .modal-content_header {
  font-weight: bold;
  color: #464c5b;
  font-size: 18px;
}
.modal .modal-content_main {
  color: #8d96a1;
  font-size: 14px;
}
.modal .modal-content_footer {
  text-align: right;
}
.router-link-active {
  background: #546573;
}
.router-link-active i {
    color: #fff;
}
.head-pic_box {
  padding: 0 10px;
}
.head-pic_box img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-left: 10px;
}
.backend-header_icon__group {
  padding: 0 20px;
}
.backend-header_icon__group i {
    font-size: 30px;
    color: #546573;
    margin-right: 30px;
    cursor: pointer;
    position: relative;
    top: 4px;
}
.backend-header {
  height: 60px;
  line-height: 60px;
}
.backend-header .title {
    background: #27cacc;
    color: #fff;
    text-align: center;
    height: 100%;
    font-weight: bold;
    font-size: 20px;
}
.backend-header .info {
    background: #eef3f7;
    height: 100%;
    box-shadow: 1px 2px 4px #ccc;
    voerflow: hidden;
}
.menu_container {
  background: #2d3f4d;
}
.menu_container .fa {
    margin: 0;
}
.menu_container a {
    display: block;
    text-align: center;
    color: #337ab7;
    padding: 30px 0;
}
.menu_container a i {
      font-size: 40px;
      margin-bottom: 10px;
}
.menu_container a i:hover {
        color: #fff;
}
.menu_container a span {
      display: block;
      color: #b5a9a9;
}
.main_container {
  padding: 20px;
  box-sizing: border-box;
}



