
* {
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
.container {
  width: 66%;
  margin: 0 auto;
}
.row::after {
  content: "";
  clear: both;
  display: block;
}
[class*="col-"] {
  float: left;
}
.fl {
  float: left;
}
.fr {
  float: right;
}

/* For desktop: */
.col-1 {
  width: 8.33%;
}
.col-2 {
  width: 16.66%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33%;
}
.col-5 {
  width: 41.66%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33%;
}
.col-8 {
  width: 66.66%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33%;
}
.col-11 {
  width: 91.66%;
}
.col-12 {
  width: 100%;
}
.offset-left-1 {
  margin-left: 8.33%;
}
.offset-left-2 {
  margin-left: 16.66%;
}
.offset-left-3 {
  margin-left: 25%;
}
.offset-left-4 {
  margin-left: 33.33%;
}
.offset-left-5 {
  margin-left: 41.66%;
}
.offset-left-6 {
  margin-left: 50%;
}
.offset-right-1 {
  margin-right: 8.33%;
}
.offset-right-2 {
  margin-right: 16.66%;
}
.offset-right-3 {
  margin-right: 25%;
}
.offset-right-4 {
  margin-right: 33.33%;
}
.offset-right-5 {
  margin-right: 41.66%;
}
.offset-right-6 {
  margin-right: 50%;
}
@media only screen and (max-width: 768px) {
  /* For mobile phones: */
[class*="col-"] {
    width: 100%;
}
.conainter {
    width: 90%;
    margin: 0 auto;
}
}
.font-header {
  width: 100%;
  height: 30px;
  line-height: 30px;
  background: #3091f2;
  box-shadow: 1px 2px 4px #ccc;
}
.font-header .router-link-active {
    background: none;
}
.font-header .search {
    font-size: 12px;
    color: #eee;
    cursor: pointer;
}
.font-header .search:active {
      color: #eee;
}
.font-header .search-icon {
    text-align: center;
}
.font-header .search-main {
    background: #fff;
    box-shadow: 1px 1px 4px #ccc;
}
.font-header .search_input__box {
    text-align: center;
    padding: 10px 0;
}
.font-header .search_input__box button {
      color: #fff;
      background: #5cadff;
      border: 1px solid #d7dde4;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
      height: 42px;
      padding: 0 40px;
      position: relative;
      left: -60px;
      cursor: pointer;
}
.font-header .search_input__box button:focus {
        outline: none;
}
.font-header .search_input__box button:active {
        background: #3091f2;
}
.font-header .search_input__box input {
      width: 50%;
      border-radius: 50px;
      height: 40px;
      border: 1px solid #d7dde4;
      padding-left: 20px;
      color: #ccc;
}
.font-header .search_input__box input:focus {
        outline: none;
}
.font-header .logo_box {
    text-align: center;
    height: 100%;
}
.font-header .logo {
    width: 40px;
    height: 30px;
}
.font-header .nav_tag {
    color: #eee;
    font-size: 12px;
    font-weight: bold;
    margin-right: 30px;
}
.font-header .nav_tag:hover {
      color: #fff;
}



