
.table {
  width: 100%;
  border-collapse: collapse;
}
.table .table-striped {
    tr-outline: none;
}
.table .table-striped tr:nth-of-type(odd) {
      background: #f9f9f9;
}
.table .table-striped tr:hover {
      outline: 1px solid #5cadff;
}
.table .table-striped td {
      font-size: 16px;
      color: #657180;
      text-align: center;
      padding: 8px;
}



