
@import url(~highlight.js/styles/atom-one-light.css);
article {
  margin-bottom: 66px;
  padding: 30px 0;
  border-bottom: 4px rgba(0, 0, 0, 0.05) solid;
}
.article_title {
  font-size: 30px;
  color: #2c3e50;
  text-align: center;
  position: relative;
}
.article_content {
  margin: 30px 0;
  font-size: 18px;
  color: #7f8c8d;
  line-height: 1.8;
}
.article_content p {
    text-indent: 2rem;
    margin-bottom: 20px;
}
.fa {
  margin-right: 10px;
}
.article_info {
  padding: 20px 0 8px 0;
  color: #555;
  text-align: center;
}
.article_info span {
    margin-right: 10px;
}
.article_info .bottom_border {
    padding-bottom: 10px;
    display: inline;
    border-bottom: 1px rgba(0, 0, 0, 0.05) dashed;
}
.article_tags {
  color: #555;
}
article {
  border-top: 1px dashed #eee;
  padding-top: 30px;
}
article pre {
    padding: 20px;
    background: #1d2b35;
    color: #fff;
    margin: 30px 0;
    letter-spacing: 0;
    font-size: 12px;
    border-radius: 4px;
    background-image: -webkit-linear-gradient(#21221d 50%, #272822 50%);
}
article img {
    max-width: 90%;
    height: auto;
    margin: 0 auto;
}
article blockquote {
    padding-left: 20px;
    margin-bottom: 20px;
    border-color: #28a745;
    font-size: 15px;
}
article blockquote p {
      text-indent: 0;
}
article ul {
    margin: 30px 0 30px 40px;
}
article li {
    list-style-type: circle;
    font-weight: bold;
}
article p {
    margin-bottom: 20px;
    letter-spacing: 0;
    text-indent: 26px;
}
article code {
    letter-spacing: 0;
}
article h1,
  article h2,
  article h3,
  article h4,
  article h5,
  article h6 {
    font-weight: bold;
    color: #2b3f4d;
    padding: 4px 0;
    margin-bottom: 10px;
}
article h1 {
    font-size: 20px;
}
article h2 {
    font-size: 18px;
}
article h3 {
    font-size: 16px;
}
article h4 {
    font-size: 14px;
}
article h5 {
    font-size: 12px;
}
article h6 {
    font-size: 10px;
}
article a {
    font-weight: bolder;
}

/*
markdown
*/
.CodeMirror {
  border-top: 1px dashed #eee;
  padding-top: 30px;
}
.CodeMirror img {
  max-width: 90%;
  height: auto;
  margin: 0 auto;
}
.CodeMirror blockquote {
  padding-left: 20px;
  margin-bottom: 20px;
}
.CodeMirror pre {
  letter-spacing: 0;
}
.CodeMirror p {
  margin-bottom: 20px;
  letter-spacing: 0;
}
.CodeMirror code {
  letter-spacing: 0;
}
.CodeMirror h1,
.CodeMirror h2,
.CodeMirror h3,
.CodeMirror h4,
.CodeMirror h5,
.CodeMirror h6 {
  font-weight: bold;
  color: #2b3f4d;
  padding: 4px 0;
  margin-bottom: 10px;
}
.CodeMirror h1 {
  font-size: 20px;
}
.CodeMirror h2 {
  font-size: 18px;
}
.CodeMirror h3 {
  font-size: 16px;
}
.CodeMirror h4 {
  font-size: 14px;
}
.CodeMirror h5 {
  font-size: 12px;
}
.CodeMirror h6 {
  font-size: 10px;
}
.CodeMirror a {
  font-weight: bolder;
}
.home_article {
  padding: 20px 0;
}
.home_article .article_content {
    max-height: 150px;
    overflow: hidden;
}
.button_box {
  text-align: center;
  margin-bottom: 40px;
}
.load-some_btn {
  border: 1px solid #D6D6D6;
  color: #ADADAD;
  border-radius: 50px;
  padding: 13px 25px;
  background: none;
  font-size: 20px;
  -webkit-transition: color 0.2s ease-out,border 0.2s ease-out,opacity 0.2s ease-out;
  transition: color 0.2s ease-out,border 0.2s ease-out,opacity 0.2s ease-out;
}
.load-some_btn:hover {
    border-color: #FFBE5B;
    color: #FFBE5B;
    cursor: pointer;
}
.load-some_btn:focus {
    outline: none;
}



