
.archive-box {
  padding: 60px 0;
}
.archive-ul {
  list-style: none;
}
.archive-ul li {
    border-bottom: 1px dashed #ccc;
    padding: 12px;
    border-radius: 4px;
    -webkit-transition: all .6s;
    transition: all .6s;
}
.archive-ul li .time {
      color: #ccc;
}
.archive-ul li a {
      display: block;
}
.archive-ul li:hover {
      background: #eee;
}
.archive-ul .article_title_box {
    color: #4c5157;
}
.archive-ul .article_title_box i {
      color: #a4a4a4;
}
.archive_pager {
  margin: 30px 0;
  overflow: hidden;
}



