
.login_title {
  margin-bottom: 60px;
  text-align: center;
}
.login-box {
  margin: 100px auto;
}
.login-box section {
    margin-bottom: 30px;
}
.login_input {
  border: 1px solid #eee;
  border-radius: 20px;
  height: 60px;
  width: 100%;
  padding-left: 70px;
  font-size: 30px;
  overflow: hidden;
  color: #ddd;
  box-sizing: border-box;
}
.login_input:focus {
    outline: none;
}
.login_section {
  position: relative;
}
.login_section i {
    font-size: 60px;
    color: #bbb;
    position: absolute;
    left: 10px;
}
.login_section .fa-unlock-alt {
    top: 4px;
}
.login_button__box {
  text-align: right;
}
.login_button {
  width: 30%;
  background: #3091f2;
  color: #fff;
  border: 1px solid #eee;
  border-radius: 6px;
  padding: 10px 16px;
}



